import React from "react"
import {navigate} from "gatsby"
import { TextField } from '@material-ui/core'
import FloatingLabelInput from 'react-floating-label-input';
import Amplify, { Auth } from 'aws-amplify';
import awsconfig from '../aws-exports';
Amplify.configure(awsconfig);
var validator = require("email-validator");


class SignInCard extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			email: "",
			pass: "",
			error: "",
			isSignedIn: false,
			remember: false,
		}
	}

	onPassEnter = (e) => {
		this.setState({[e.target.id]: e.target.value})
		console.log(e.target.value)
	}
	onEmailEnter = (e) => {
		this.setState({[e.target.id]: e.target.value.toLowerCase()})
		console.log(e.target.value)
	}

	SignIn = () => {
		let email = this.state.email;
		let pass = this.state.pass;
	    Auth.signIn({username: email, password: pass})
	    	.then((response)=>{
	    		this.setState({isSignedIn: true})
	    		navigate("/dashboard", {state:{clientId: response.pool.clientId, email: this.state.email}})

	    	})
	    	.catch((err)=> {
	    		this.setState({error: err.message})
	    		console.log(err)
	    	})
	}

	CheckBox = () => {
		let current = this.state.remember
		this.setState({remember: !current})
	}

	componentDidMount() {
		if (typeof window !== 'undefined') {
			this.setState({email:windowGlobal.localStorage.getItem("email")})
			this.setState({pass:windowGlobal.localStorage.getItem("pass")})
		}
	}

	render() {
		if (this.state.remember && (typeof window !== 'undefined')) {
			windowGlobal.localStorage.setItem("email", this.state.email)
			windowGlobal.localStorage.setItem("pass", this.state.pass)
		}
		const OnSubmit = () => {
			if (this.state.email === "email" || this.state.email==="" || this.state.pass==="" || this.state.pass === "pass") {
				this.setState({error: "Please fill in all the details!"})
			} else if (validator.validate(this.state.email) !== true) {
				this.setState({error: "Email format incorrect."})			
			} else {
				this.SignIn()
			}
		}
		return(
			<div className="w-100 shadow-3 tc bg-white ba b--light-gray Avenir" style={{margin:"auto", height: "850px", "min-width":"750px", }}>
				<h1 className="f1 mt5" style={{color: "rgb(127,90,179)"}}>CORONA.AI</h1>
				<p className="f3 gray mt4 mb5">Please Sign In or Create an Account</p>
				<div className="tl" style={{ fontSize: 24, padding:"20px 20px", "border-radius":"15px", width:"75%", "margin-top":"20px", "margin":"auto"}}>
		            <FloatingLabelInput
		              id="email"
		              label="Email ID"
		              placeholder=""
		              value={this.state.email}
		              onChange={this.onEmailEnter}		        
		            />
		        </div>
		        <div className="tl" style={{ fontSize: 24, padding:"20px 20px", "border-radius":"15px", width:"75%", "margin-top":"20px", "margin":"auto"}}>
		            <FloatingLabelInput
		              id="pass"
		              label="Password"
		              placeholder=""
		              value={this.state.pass}
		              type="password"
		              onChange={this.onPassEnter}		        
		            />
		        </div>
				<p className="f5 b red">{this.state.error}</p>
				<br/>
				<div className="w-100 mb5 f4 Avenir">
					<input type="checkbox" onClick={()=>this.CheckBox()} name="remember" className="pointer dib"/>
					<p className="f4 dib ml2 mr5">Remember me </p>
					<p onClick={()=> navigate("/forgot-password")} className="f4 black underline-hover pointer dib ml5">Forgot password?</p>
				</div>

				<p onClick={()=>OnSubmit()} class="f4 tc no-underline black bg-animate hover-bg-purple hover-white inline-flex pointer mb3 items-center pa3 ba border-box mr5" style={{"margin-left":"auto"}}>
				    <span class="pr1"> Sign In </span>
				</p>
				<p onClick={()=> navigate("/register")} class="f4 tc no-underline black bg-animate hover-bg-purple hover-white inline-flex pointer mb3 items-center pa3 ba border-box ml5">
				    <span class="pr1"> Sign Up </span>
				</p>
				<br/>
				<p class="f4 tc no-underline bg-animate bg-light-purple light-gray inline-flex pointer mb3 items-center grow mt4 bg-white black pa3" style={{"margin-left":"auto"}}>
				    <span class="pr1"> Invite Others </span>
				</p>
			</div>
		);
	}
}

export default SignInCard;